.header {
  height: 80vh;
  width: 100%;
  background-image:url(../images/bg-0.jpg);
  background-position-x: 62.5%;
  background-size: cover;
  position: relative;
  overflow: hidden;
  transition: filter 1.5s;
  filter: brightness(1.5);
  &.on {
    filter: brightness(1);
  }
  @include media-breakpoint-up(sm) {
    height: 70vh;
    background-position-x: 80%;
  }
  @include media-breakpoint-up(lg) {
    max-height: 1024px;
    height: 100vh;
  }
  //手機橫式
  @media (max-height: 414px) {
    height: 100vh;  
  }
}
.fancy-menu {
  position: absolute;
  top: 0.5em; 
  right: 0.5em;
  z-index: 10;
  display: inline-block;
  background-color: $primary;
  span {
    background: $gray-100;
    border-radius: 3px;
    display: block;
    height: 2px;
    margin-top: 5px;
    padding: 0;
    position: relative;
    transition: all 0.2s ease;
    width: 25px;
    &:last-child {
      margin-bottom: 5px;
    }
  }
  &.active {
    span {
      &:nth-of-type(1) {
        transform: rotate3d(0, 0, 1, 45deg);
        top: 7px;
      }
      &:nth-of-type(2) {
        opacity: 0;
      }
      &:nth-of-type(3) {
        transform: rotate3d(0,0,1,-45deg);
    top: -7px;
      }
    }
  }
}
.navbar {
  position: fixed;
  width: 100%;
  padding: 0;

  z-index: 5;
  transition: all .5s;
  transform: translateY(-80px);
  opacity: 0;
  &-collapse {
    position: absolute;
    top: 0;
    bottom: 0;
    height: 100vh;
    width: 100%;
    background-color:white;
    opacity: 0;
    transition: all .3s;
    &.show {
      opacity: 1;
    }
  }
  &-nav {
    height: 100vh;
    justify-content: center;
    align-items: center;
    li {
      margin-left: 1em;
      &:first-child{
        margin-left: 0;
      }
    }
    .nav-link.nav-link {
      color: $primary;
      font-size: 1.25em;
      letter-spacing: 5px;  
      padding: .25em .5em; 
      text-indent: 5px; 
      margin-bottom: .5em; 
      margin-top: 0.5em;
      position: relative;
      &::before {
        position: absolute;
        content: " ";
        transition: all .3s;
        background-color: $secondary;
        height: 1px;
        width: 0%;
        margin: auto;
        bottom: 0;
        left: 0;
        right: 0;
        display: block;
      }
      &:hover,&.active { 
        &::before {
          width: 85%;
        }
      }
    }
  }
  &.fixed {
    opacity: 1;
    transform: translateY(0);
  }
  @include media-breakpoint-up(lg) {

    &-collapse {
      height: auto;
      bottom: auto;
      background-color: rgba($color: white, $alpha: .8);
      opacity: 1;
    }
    &-nav {
      height: auto;
      max-width: 1136px;
      width: 100%;
      margin-left: auto;
      margin-right: auto;
      // justify-content: space-around;
      a.nav-link.nav-link {
        font-size: 1.2em;
        margin-top: 1em;
        margin-bottom: 1em;
      }
      .nav-item {
        &:last-child {
          a.nav-link {
            font-size: 1em;
          }
        }
      }
    }
    &.fixed {
      background-color: rgba($primary,.5);
      top: 0;
    }
  }
}
.head-menu {
  position: absolute;
  top: 0;
  bottom: 0;
  width: 70%;
  display: flex;
  flex-direction: column;
  justify-content: flex-start;
  align-items: center;
  padding: 1em;
  transition: all .5s;
  transform: translateX(-50%);
  opacity: 0;
  &.on {
    transform: translateX(0);
    opacity: 1;
  }
  @include media-breakpoint-up(sm) {
    width: 50%;
  }
  @include media-breakpoint-up(lg) {
    justify-content: center;
    background-image: linear-gradient(90deg, rgba(white, 1) 0%, rgba(white, .85) 50%, rgba(white, 0) 100%);
  }
}
.logo {
  width: 50%;
  max-width: 244px;
  margin-bottom: .5em;
  transition: all 1s ease ;
  transform: translateX(-100%);
  opacity: 0;
  &.on {
    transform: translateX(0);
    opacity: 1;
  }
  img {
    display: block;
    max-width: 100%;
    height: auto;
  }
  @include media-breakpoint-up(sm) {
  }
  @include media-breakpoint-up(lg) {
    margin-bottom: 1.5em;
  }
}
.tit {
  width: 80%;
  max-width: 330px;
  margin-bottom: 1.5em;
  transition: all 1s ease ;
  transform: translateX(-100%);
  opacity: 0;
  &.on {
    transform: translateX(0);
    opacity: 1;
  }
  img {
    display: block;
    width: 100%;
    height: auto;
  }
  @include media-breakpoint-up(sm) {
    width: 70%;
  }
}
.nav-head {
  flex-direction: column;
  justify-content: center;
  align-items: center;
  opacity: 0;
  a {
    font-size: 1.075em;
    color: $secondary;
    margin-top: .5em;
    margin-bottom: .5em;
    position: relative;
    letter-spacing: 2px;
    transition: all .3s;
    &::before,&::after {
      position: absolute;;
      content: "";
      margin: auto;
      top: 0; 
      bottom: 0;
      height: 1px;
      width: 0px;
      background-color: $secondary;
      display: block; 
      transition: all .3s;
    }
    &::before {
      left: -10px;
    }
    &::after {
      right: -10px;
    }
    &:hover {
      letter-spacing: 5px;
      &::before,&::after {
        width: 20px;
      }
    }
  }
  @include media-breakpoint-up(lg) {
    transition: all 1s ease ;
    transform: translateX(-100%);
    opacity: 0;
    &.on {
      transform: translateX(0);
      opacity: 1;
    }
  }
}
.btn-act {
  position: relative;;
  display: inline-block;
  color: white;
  background-color: $secondary;
  padding: 1em 1.5em;
  transition: all .3s;
  &::before {
    content: '';
    position: absolute;
    // display: inline-block;
    width: 100%;
    height: 100%;
    top: 0.3em;
    left: 0.2em;
    border: 1px solid $secondary;
    transition: all .3s;
  }
  &:hover {
    text-decoration: none;
    color: white;
    background-color: darken($secondary, 10);
    &::before {
      top: 0.45em;
      left: 0.35em;
      // border-color:lighten($primary,  5);
    }
  }
}
.btn-act2  {
  font-family: 'Noto Serif TC';
  font-weight: bold;
  background-color: $light;
  font-size: 1.75em;
  padding: 1.5em;
  position: relative;
  &::before {
    position: absolute;
    content: '';
    margin: auto;
    top: 0;
    bottom: 0;
    left: 0;
    right: 0;
    width: calc(100% - 1em);
    height: calc(100% - 1em);
    border: 1px solid white;
    transition: all .3s;
  }
  &:hover {
    background-color: darken($light, 10);
    text-decoration: none;
    &::before {
      width: calc(100% - 0.5em);
      height: calc(100% - 0.5em);
    }
  }
}
