.tit-text {
  // margin-left: 2em;
  position: relative;
  &::before, &::after {
    position: absolute;
    content: "";
  }
  &::before {
    margin: auto;
    top: 0;
    bottom: 0;
    width: 1em;
    height: 1em;
    transform: translateX(-150%);
    background-color: $secondary;
  }
  &::after {
    width: 1.75em;
    height: 1.75em;
    bottom: .5em;
    background-image: url(../images/tit-bg1.svg);
    background-size: cover;
    transform: translateX(10%);
  }
  h3 {
    font-size: 1.25em;
    display: inline-block;
    color: white;
    letter-spacing: .2em;
    padding: .5em 1em;
    position: relative;
    z-index: 0;
    &::before {
      position: absolute;
      content: '';
      display: block;
      margin: auto;
      top: 0;
      bottom: 0;
      left: 0;
      right: 0;
      width: 100%;
      background-color: $primary;
      transform: skewX(-25deg);
      z-index: -1;
    }
  }
  @include media-breakpoint-up(md) {
    h3 {
      font-size: 1.5em;
    }
  }
  @include media-breakpoint-up(lg) {
    &::before {
      width: 1.5em;
      height: 1.5em;
    }
    &::after {
      width: 2.25em;
      height: 2.25em;
    }
    h3 {
      font-size: 2em;
    }
  }
}
.tit-text2 {
  position: relative;
  z-index: 0;
  // margin-bottom: 3em;
  p {
    display: inline-block;
    font-size: 1.25em;
    background-color: $secondary;
