    width: 5em;
    height: 5em;
    top: -1.5em;
    left: 1em;
    z-index: -1;
  }
  &::after{
    position: absolute;
    content: '';
    display: block;
    margin: auto;
    top: 0;
    bottom: 0;
    height: 2px;
    width: 100%;
    background-color: $secondary;
    z-index: -1;
  }
  &.blue {
    p {
      color: white;
      background-color: $primary;
    }
    &::before{
      background-image: url('../images/tit-bg3.png');
    }
    &::after {
