}
.num {
  text-align: center;
  font-size: 1.75em;
  margin-top: 1em;
  margin-bottom:2em;
  position: relative;
  &::before, &::after {
    position: absolute;
    font-size: 0.25em;
    color: white;
    background-color: $primary;
    padding: .25em 1em;
    margin: auto;
  }
  &::before {
    content: '已有';
    transform: translateX(-100%) translateY(50%);
  }
  &::after {
    content: '位加入';
    transform: translateY(50%);
  }
  >span {
    color: $primary;
    padding: 0 .25em;
    font-weight: 300;
  }
  @include media-breakpoint-up(sm) {
    &::before {
      transform: translateX(-100%) translateY(150%);
    }
    &::after {
      bottom: 0;
      transform: translateY(-100%);
    }
    >span {
      font-size: 2.25em;
    }
  }
  @include media-breakpoint-up(md) {
    &::before, &::after {
      font-size: .5em;
    }
    &::after {
      content: '位救世主加入';
    }
    >span {
      font-size: 3em;
    }
  }
  @include media-breakpoint-up(xl) {
    >span {
      font-size: 4em;
    }
  }
}
.progress {
  width: 90%;
  margin-left: 5%;
  margin-bottom: 150px;
  position: relative;
  &::before {
    position: absolute;
    content: 'START';
    font-weight: bold;
    transform: translateX(-115%) translateY(-25%);
  }
  &-bg {
    border: 2px solid darken($primary,10);
    background:rgba($color:$white, $alpha: .5); 
    height:14px; 
    overflow: hidden;
  }
  &-bar {
    height:10px; 
    width: 0%;
    background-color: $primary;
    transition: width 3s ease !important;
  }
}
.gift-progress {
  width: 100%;
  position: relative;
  list-style: none;
  li {
    position: absolute;
    transform: translateX(-50%);
    width: 15%;
    text-align: center;
    margin-top: 1em;
    opacity: .6;
    filter: grayscale(1);
    &:nth-child(1){
      left: 20%;
      &::after {
        content: '1000';
      }
    }
    &:nth-child(2){
      left: 40%;
      &::after {
        content: '5000';
      }
    }
    &:nth-child(3){
      left: 60%;
      &::after {
        content: '10000';
      }
    }
    &:nth-child(4){
      left:80%;
      &::after {
        content: '15000';
      }
    }
    &:nth-child(5){
      left:100%;
      &::after {
        content: '30000';
      }
    }
    &::before {
      position: absolute;
      margin: auto;
      left: 0;
      right: 0;
      top: -30px;
      content: '';
      height: 30px;
      width: 2px;
      background-color: darken($primary,10);
    }
    &::after {
      position: absolute;
      margin: auto;
      left: 0;
      right: 0;
      top:-50px;
      font-size: 0.8em;
    }
    &.on {
      opacity: 1;
      filter: grayscale(0);
    }
    @include media-breakpoint-up(sm) {
      &:nth-child(1){
        &::after {
          content: '1,000人';
        }
      }
      &:nth-child(2){
        &::after {
          content: '5,000人';
        }
      }
      &:nth-child(3){
        &::after {
          content: '10,000人';
        }
      }
      &:nth-child(4){
        &::after {
          content: '15,000人';
        }
      }
      &:nth-child(5){
        &::after {
          content: '30,000人';
        }
      }
    }
    small {
      display: block;
      margin-top: 1em;
      margin-bottom: 1em;
    }
  }
}

