  }
  &-gift {
    position: relative;
    padding-top: 1em;
    filter: grayscale(1);
    transform: translateY(100%);
    opacity: .5;
    transition: all .5s ease !important;
    .text {
      display: flex;
      flex-wrap: wrap;
      h6 {
        width: 100%;
        font-size: 1.5em;
        font-family: Arial ,'Noto Sans TC';
