          opacity: 0;
        }
      }
      a {
        padding: 0.25em;
        border: 1px solid $secondary;
        display: block;
        border-radius: 50%;
        transition: all .3s;
        > img {
          padding: .25em ;
        }
        &:hover {
          transform: scale(1.05);
          background-color: $light;
        }
      }
      p {
        font-size: 0.8em;
        margin-top: 0.5em;
      }
    }
    &:after {
      content: "";
      position: absolute;
      top: 0;
      left: 50%;
      transform: translateX(-50%);
      width: .75em;
      height: .75em;
      border-radius: 50%;
      background-color: $primary;
      z-index: 1;
    }
    &.on {
      filter: grayscale(0);
      opacity: 1;
      .text {
        h6 {
          &::after {
            opacity: 1;
          }
        }
      }
    }
    &:last-child {
      margin-bottom: 250px;
    }
    @include media-breakpoint-up (md) {
      .text {
        a {
          padding: .5em;
          >img {
            padding: 0.5em;
          }
        }
        h6 {
