.invite-btn {
  display: flex;
  justify-content: center;
  align-items: center;
  flex-wrap: wrap;
  background-color: white;
  padding: 1em;
  margin-top: 2em;
  margin-bottom: 2em;
  border-top: 3px solid $primary;
  border-bottom: 3px solid $primary;
  position: relative;
  >p {
    width: 100%;
    text-align: center;
  }
  &::before,&::after {
    position: absolute;
    content: "";
    width: 100%;
    height: 1px;
    background-color: $primary;
  }
  &::before {
    top: 5px;
